<template>
    <div class="yingyong">
        <headerBar :title="headerNavData"></headerBar>
        <div class="applycontent">
          <div class="app_list">
            <span class="app_label">{{$t('code.mingcheng')}}</span>
            <input type="text" v-model="applydata.remark" class="app_inp"/>
          </div>
          <div class="app_list">
            <span class="app_label">{{$t('placeholder.codeaddress')}}</span>
            <input type="text" v-model="applydata.target" class="app_inp"/>
          </div>
          <button class="app_btn" 
            :class="{'app_btn_bj' : applydata.remark && applydata.target}" 
            @click="applydata.remark && applydata.target && ApplyUpdate()">{{$t('data.add')}}</button>
        </div>
    </div>
  
</template>

<script>
import {getCookie,setCookie} from '../assets/js/cookie.js'
  import headerBar from './header.vue'
  import footerBar from './footer.vue'
export default {
  components:{
      "headerBar":headerBar,
      "footerBar":footerBar,
  },
  data () {
    return {
      headerNavData:{
         left:true,
         title:this.$t('data.addaddress'),
         right:false,
         bj:'#2c94ed',
         search:false,
         share:false
      },
      applydata:{
        remark:'',
        target:'',
        collector:getCookie("publickey"),
        req:1
      }
    }
  },
  methods:{
    ApplyUpdate(){
      var that = this;
      this.openloading(this.$t("data.submitting"))
      this.axios({
        url:this.Url + 'AddressBook.ashx',
        method:'post',
        params: this.applydata
      }).then(function (res) {
        that.closeloading()
        if(res.data.success){
          that.Toast(res.data.msg)
          setTimeout(function(){
            // that.$router.push({path:'/addresbook'})
            that.$router.go(-1)
          },1500)
        }else{
          that.Toast(res.data.msg)
        }
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.yingyong{
  padding-top: .9rem;
  padding-bottom: .3rem;
}
.mt20{
  margin-top: .3rem;
}
.apl_p{
  background: #dfeffd;
  font-size: .3rem;
  color: #278eed;
  text-align: center;
  padding: .2rem 0
}
.app_list{
  padding: 0 .3rem;
  margin-top: .3rem;
}
.app_list .app_label{
  display: block;
  color: #151515;
  font-size: .3rem;
}
.app_list .app_inp{
  display: block;
  color: #666;
  font-size: .28rem;
  margin-top: .1rem;
  border-radius: 4px;
  border: 1px solid #e4e4e4;
  height: .8rem;
  padding: 0 .3rem;
  text-align: left;
  line-height: .8rem;
  width: 100%
}
.uploadimg{
  width: 1.48rem;
  height: 1.48rem;
  border: 1px solid #e4e4e4;
  text-align: center;
  line-height: 1.5rem;
  color: #999;
  font-size: .3rem;
  margin-top: .1rem;
  cursor: pointer;
  display: inline-block;
  margin-right: .2rem;
}
.app_btn{
  font-size: .3rem;
  width: 4.58rem;
  height: .8rem;
  line-height: .8rem;
  text-align: center;
  border: none;
  outline: none;
  margin: .5rem auto;
  display: block;
  background: #f1f1f1;
  border-radius: 4px;
  color: #888;
}
.app_btn_bj{
  background: #278eed!important;
  color: #fff;
}
.uploadimg{
  position: relative;
  overflow: hidden;
}
.uploadimg_bj{
  background: url('../assets/images/addimg.png') no-repeat center center;
  background-size: .8rem .8rem;
}
.uploadimg input{
  position: absolute;
  top: 0;
  left: 0;
  width: 1.48rem;
  height: 1.48rem;
  opacity: 0;
}
.uploadimg img{
  width: 100%;
}
</style>



